<template>
  <div>
    <Table :columns="columns" :loading="loading" :data="list">
      <template slot-scope="{ row }" slot="svg">
        <div style="font-size:60px;" v-html="row.svg"></div>
      </template>
      <template slot-scope="{ row }" slot="username">
        <div >{{row.user.username}}</div>
      </template>
      <template slot-scope="{ row }" slot="type">
        <div  >{{types.filter(e=>e.id==row.typeId)[0].name}}</div>
      </template>
      <template slot-scope="{ row, index }" slot="action">
        <!-- <Button size="small" style="margin-right: 5px"></Button> -->
        <Button
          type="primary"
          size="small"
          style="margin-right: 5px"
          @click="e=>$router.push(`/addBlog/${row.id}`)"
        ></Button>
        <Button type="error" size="small" @click="removeBlog(index)"></Button>
      </template>
    </Table>
  </div>
</template>

<script>
import axios from "axios";
import {Table,Button} from 'view-design'
export default {
  components:{Table,Button},
  data() {
    return {
      types:[],
      loading:false,
      columns: [
        {
          title: "",
          slot: "svg",
          width:100,
        },
        {
          title: "",
          slot: "username",
          width:150,
        },
        {
          title: "",
          slot: "type",
          width:100,
        },
        {
          title: "",
          key: "title"
        },
        {
          title: "",
          key: "intro"
        },
        {
          title: "",
          slot: "action"
        }
      ],
      list: []
    };
  },
  async created() {
    await this.getTypes()
    await this.getBlogs()
  },
  methods: {
    async getTypes() {
      const res = await axios.get("/api/types")
      this.types = res.data;
    },
    async getBlogs() {
      this.loading = true
      const res = await axios.get("/api/blogs")
      this.list = res.data
      this.loading = false
    },
    async removeBlog(idx) {
        const res = await axios.delete(`/api/blogs/${this.list[idx].id}`)
        this.list.splice(idx,1)
    }
  }
};
</script>

<style lang="scss" >
.ivu-table table{
  min-height: 300px;
}
</style>